.logoutContainer{
    /* margin-top: 200px; */
    display: flex;
    height: 80vh;
    align-items: center;
    justify-content: center;
    font-family: "Montserrat", sans-serif;
    /* background-color: red; */
}

.logout{
    width: 60%;
    background-color: white;
    box-shadow: -2px 4px 8px rgba(0, 0, 0, 0.25);
    border-radius: 4px;
    padding: 28px 24px 36px 24px;
   
}

.formInstruction{
    margin-bottom: 160px;
}

.logoutButton {
    background-color: #FDAF33;
    border: none;
    outline: none;
    padding: 16px 8px;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    cursor: pointer;
    width: 100%; /* Combine both yellowBtn and long styles */
}

.logoutButton:hover {
    background-color: #E8961C; /* Optional: darker shade on hover */
}
